export default function AccountingWithProfitsCash() {
  const applicationState = useSelector(selectApplication);
  const dispatch = useDispatch();
  const email = applicationState?.email;
  const userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");

  const { data: userProjects, loading, error } = useFirebaseNode("usersCraft/" + userEmail + "/posts/");

  React.useEffect(() => { dispatch(emptyTempData()) }, []);

  if (!!loading) { return <div>...</div> }
  if (!userProjects) { return <NoPrjectLayout /> }

  let myAccountingWithProfitsCashProjects = Object.keys(userProjects)
    .map((item) => userProjects[item])
    .filter((item) => item.type === "accountingwithprofitscash")


  return <CheckForProjectSelection
    userEmail={userEmail}
    myAccountingWithProfitsCashProjects={myAccountingWithProfitsCashProjects}
  />
}

function CheckForProjectSelection(props) {
  const firenode = useSelector(selectTempData).firenode;
  let id = !!firenode ? firenode : "accountingwithprofitscash";

  return <SomeProjectsFoundLayout key={id} firenode={id} {...props} />
}

function SomeProjectsFoundLayout({ firenode, userEmail, myAccountingWithProfitsCashProjects }) {
  const { data: currentProjectObject, loading, error } = useFirebaseNode("usersCraft/" + userEmail + "/posts/" + firenode);

  if (!!loading || !currentProjectObject) { return <div>...</div> }

  return <LoadProjectDataToState currentProjectObject={currentProjectObject} myAccountingWithProfitsCashProjects={myAccountingWithProfitsCashProjects} />
}

function isObject(val) {
  if (val === null) { return false; }
  return ((typeof val === 'function') || (typeof val === 'object'));
}

function LoadProjectDataToState({ currentProjectObject, myAccountingWithProfitsCashProjects }) {
  const dispatch = useDispatch();
  const [loaded, setLoaded] = useState(false);
  const [htmlfeedback, setHtmlfeedback] = useState(currentProjectObject.comment);

  function updateHtmlContent(htmlfeedback) {
    dispatch(set_currentProject({
      //   currentProjectTitle: item.theme, //title,
      currentProjectComment: htmlfeedback
      //   currentProjectMediaAndDataAndTemplatesURL: item.id,
      //   currentProjectSourseDataURL: item.answer, //content,
      //   currentProjectMoneyScale: item.quizString,
      //   currentProjectReportIndicatorsDictionary: item.content
      // }));
    }))
  }

  console.log(currentProjectObject);

  React.useEffect(() => {
    let records = !!currentProjectObject?.content ? currentProjectObject.content.map(item => { return item }) : [];
    console.log(records);
    dispatch(set_spreadsheetTitle({ spreadsheetTitle: currentProjectObject.title }));
    if (isObject(currentProjectObject.quizString)) {
      console.log(currentProjectObject.quizString);
      console.log(createProtoArray(currentProjectObject.quizString));
      dispatch(load_data({ protoData: createProtoArray(currentProjectObject.quizString) }))
    };
    dispatch(setTempFireNode(currentProjectObject.id));
    dispatch(setTempDataArrayItems(records));

    dispatch(setTempType(currentProjectObject.title));

    setLoaded(true)
  }
    , []);

  if (!loaded) { return <div>...</div> }

  return <div>
    <AccountingNavBar myAccountingWithProfitsCashProjects={myAccountingWithProfitsCashProjects} />
    <AccountingReports />
    <Container className="mt-3 mb-3">
      <HTMLEditorClassic htmlfeedback={htmlfeedback}
        setHtmlfeedback={updateHtmlContent} />
    </Container>
    {/* <AddMediaItemToPostsGlobalState /> */}
    <AccountingMachine />
  </div>
}

function NoPrjectLayout() {
  const dispatch = useDispatch();
  const [loaded, setLoaded] = useState(false);
  const [htmlfeedback, setHtmlfeedback] = useState("Комментарий");

  function updateHtmlContent(htmlfeedback) {
    dispatch(set_currentProject({
      //   currentProjectTitle: item.theme, //title,
      currentProjectComment: htmlfeedback
      //   currentProjectMediaAndDataAndTemplatesURL: item.id,
      //   currentProjectSourseDataURL: item.answer, //content,
      //   currentProjectMoneyScale: item.quizString,
      //   currentProjectReportIndicatorsDictionary: item.content
      // }));
    }))
  }

  React.useEffect(() => {
    dispatch(setTempFireNode("accountingwithprofitscash"));
    dispatch(setTempDataArrayItems([]));
    dispatch(set_spreadsheetTitle("Расчеты по проекту"));
    dispatch(setTempType("Бюджетирование по трем формам формам"));

    setLoaded(true)
  }
    , []);

  if (!loaded) { return <div>...</div> }

  return <div>
    <AccountingNavBar />
    <AccountingReports />
    <Container className="mt-3 mb-3">
      <HTMLEditorClassic htmlfeedback={htmlfeedback}
        setHtmlfeedback={updateHtmlContent} />
    </Container>
    <AccountingMachine />
  </div>
}

function AccountingNavBar({ myAccountingWithProfitsCashProjects = [] }) {
  const applicationState = useSelector(selectApplication);
  const dispatch = useDispatch();
  const user = applicationState?.user;
  const email = applicationState?.email;
  const comment = applicationState?.currentProjectComment;
  const avatarUrl = applicationState?.avatarUrl;
  const records = useSelector(selectTempData).dataArray;
  const projectTitle = useSelector(selectTempData).type;
  const id = useSelector(selectTempData).firenode;
  const content = useSelector(selectSpreadsheetProtoData);
  const [showNewProject, setShowNewProject] = useState(false);
  const [projectComment, setProjectComment] = useState("Лекционный пример");

  function onSave() {

    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = !!id ? id : firebase.database().ref(userEmail).child('posts').push().key;

      let postObject = {
        id: idPost, // "accountingwithprofitscash",
        title: projectTitle, // "Планирование и бюджетирование по трем формам"
        theme: "Планирование и бюджетирование", //data.currentProjectTitle.length > 2 ? data.currentProjectTitle : 'Проект',
        answer: "Операции и прогнозная отчетность",
        comment: comment, // projectComment, // "Лекционный пример",
        type: "accountingwithprofitscash",
        content: records,
        quizString: createProtoObject(content),
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      //     console.log(postObject);
      //    dispatch(createPost(postObject));
      var updates = {};
      updates[
        "/usersCraft/" + userEmail + "/posts/" + idPost
      ] = postObject;
      updates['/openmedia/' + idPost] = postObject;
      updates["/usersTemplates/projects/" + userEmail + '/' + idPost] = postObject;

      return firebase
        .database()
        .ref()
        .update(updates)
        .then(() => {
          //      DoSavedSuccessfully(true);
          //      doEditPostTitleComment(false);
        });
    }
  }

  const handleShowNewProject = () => setShowNewProject(true);
  const handleCloseNewProject = () => setShowNewProject(false);

  function setFormObject(data) {
    dispatch(setTempFireNode(firebase.database().ref(email.replace(/[^a-zA-Z0-9]/g, "_")).child('posts').push().key));
    dispatch(setTempDataArrayItems([]));
    dispatch(set_spreadsheetTitle(data.title));
    dispatch(setTempType(data.title));
    setProjectComment(data.comment);
  }

  return <>
    <Navbar className="bg-light justify-content-between mb-3">
      <Navbar.Brand>{projectTitle}</Navbar.Brand>
      {myAccountingWithProfitsCashProjects.length < 2 ? null :
        <DrowpdownWithUserProjects
          myAccountingWithProfitsCashProjects={myAccountingWithProfitsCashProjects}
        />}
    </Navbar>
    <Navbar className="bg-light justify-content-between mb-3">
      <Button
        variant="outline-secondary"
        size="sm"
        //  disabled={updatedRecords.length === 0}
        onClick={() => onSave()}
      >
        Сохранить
</Button>
      <Button
        variant="outline-secondary"
        size="sm"
        //     disabled={records.length === 0}
        onClick={() => handleShowNewProject()}
      >
        +Новый
        </Button>
    </Navbar>
    <Modal
      show={showNewProject}
      onHide={() => handleCloseNewProject()}
      size="lg"
    >
      <Modal.Header closeButton>
        <Modal.Title>Сведения о проекте</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <NewProjectForm setFormObject={setFormObject} />
      </Modal.Body>
      {/*    <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>Закрыть</Button> 
      </Modal.Footer> */}
    </Modal>
  </>
}

function DrowpdownWithUserProjects({ myAccountingWithProfitsCashProjects = [] }) {
  const dispatch = useDispatch();
  return <SplitButton
    size="sm"
    drop="left"
    variant="outline-secondary"
    title="Выбрать"
  >
    {" "}
    {myAccountingWithProfitsCashProjects.map((item, index) => (
      <Dropdown.Item
        key={item.id}
        eventKey={index}
        onClick={() => {
          dispatch(setTempFireNode(item.id));
          // dispatch(set_currentProject({
          //   currentProjectTitle: item.theme, //title,
          //   currentProjectComment: item.comment,
          //   currentProjectMediaAndDataAndTemplatesURL: item.id,
          //   currentProjectSourseDataURL: item.answer, //content,
          //   currentProjectMoneyScale: item.quizString,
          //   currentProjectReportIndicatorsDictionary: item.content
          // }));
        }}
      >
        {item.title}
      </Dropdown.Item>
    ))}
  </SplitButton>
}

function AccountingReports() {
  const records = useSelector(selectTempData).dataArray;
  const [open, setOpen] = useState(false);

  return <div>
    <ShowBalance records={records} />
    <hr />
    <ShowFinancialResults records={records} />
    <hr />
    <ShowCashFlow records={records} />
    <hr />
    <SpreadsheetLayout />
    <hr className="mb-3 mt-3" />
    <Button
      onClick={() => setOpen(!open)}
      aria-controls="example-collapse-text"
      aria-expanded={open}
      variant="outline-secondary"
      className="mb-3 mt-3"
    >
      {open ? "Скрыть Журнал" : "Показать журнал"}
    </Button>
    <Collapse in={open}>
      <div id="example-collapse-text">
        <RecordsList records={records} />
      </div>
    </Collapse>
  </div>
}

function AccountingMachine() {
  const dispatch = useDispatch();
  const { register, handleSubmit, reset, watch } = useForm(); //, watch, errors
  const [periods, setPeriods] = useState(["2021", "2022", "2023", "2024"]);

  const applicationState = useSelector(selectApplication);
  const user = applicationState?.user;
  const email = applicationState?.email;
  const comment = applicationState?.currentProjectComment;
  const avatarUrl = applicationState?.avatarUrl;
  const records = useSelector(selectTempData).dataArray;
  const projectTitle = useSelector(selectTempData).type;
  const id = useSelector(selectTempData).firenode;
  const content = useSelector(selectSpreadsheetProtoData);


  function onSave(updatedRecs) {

    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = !!id ? id : firebase.database().ref(userEmail).child('posts').push().key;

      let postObject = {
        id: idPost, // "accountingwithprofitscash",
        title: projectTitle, // "Планирование и бюджетирование по трем формам"
        theme: "Планирование и бюджетирование", //data.currentProjectTitle.length > 2 ? data.currentProjectTitle : 'Проект',
        answer: "Операции и прогнозная отчетность",
        comment: comment, // projectComment, // "Лекционный пример",
        type: "accountingwithprofitscash",
        content: updatedRecs,
        quizString: createProtoObject(content),
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      //     console.log(postObject);
      //    dispatch(createPost(postObject));
      var updates = {};
      updates[
        "/usersCraft/" + userEmail + "/posts/" + idPost
      ] = postObject;
      updates['/openmedia/' + idPost] = postObject;
      updates["/usersTemplates/projects/" + userEmail + '/' + idPost] = postObject;

      return firebase
        .database()
        .ref()
        .update(updates)
        .then(() => {
          //      DoSavedSuccessfully(true);
          //      doEditPostTitleComment(false);
        });
    }
  }

  const onSubmit = (data) => {
    //     updateRecords((draft) => { draft.push(data)}); 
    dispatch(pushTempDataArrayItem(data));
    reset({ d: "...", k: "...", sum: 0 });
    setTimeout(onSave([...records, data]), 1000);

    //   console.log(data);
  };

  const capitalIncrease = watch("k") === "Нераспределенная прибыль" ? true : false;
  const capitalDecrease = watch("d") === "Нераспределенная прибыль" ? true : false;
  const cashIncrease = watch("d") === "Деньги" ? true : false;
  const cashDecrease = watch("k") === "Деньги" ? true : false;
  const costsCalculation = watch("d") === "Незавершенное производство" ? true : false;

  return (
    <div>
      <div className="p-3 mb-2 bg-secondary text-white">Новая операция</div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Form.Row>
          <Form.Group as={Col} controlId="formStatePeriod">
            <Form.Label>Период</Form.Label>
            <Form.Control as="select" name="period" ref={register} required>
              {["...", ...periods].map((item) => (
                <option key={item}>{item}</option>
              ))}
            </Form.Control>
          </Form.Group>
        </Form.Row>
        <Form.Row>
          <Form.Group as={Col} controlId="formStateD">
            <Form.Label>Актив+ или Пассив- (Дт)</Form.Label>
            <Form.Control as="select" name="d" ref={register} required>
              <option>...</option>
              <option>Основные средства</option>
              <option>Материалы</option>
              <option>Незавершенное производство</option>
              <option>Готовая продукция</option>
              <option>Дебиторская задолженность</option>
              <option>Деньги</option>
              <option>Уставный капитал</option>
              <option>Нераспределенная прибыль</option>
              <option>Долгосрочный банковский кредит</option>
              <option>Краткосрочный банковский кредит</option>
              <option>Кредиторская задолженность</option>
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="formStateK">
            <Form.Label>Актив- или Пассив+ (Кт)</Form.Label>
            <Form.Control as="select" name="k" ref={register} required>
              <option>...</option>
              <option>Основные средства</option>
              <option>Материалы</option>
              <option>Незавершенное производство</option>
              <option>Готовая продукция</option>
              <option>Дебиторская задолженность</option>
              <option>Деньги</option>
              <option>Уставный капитал</option>
              <option>Нераспределенная прибыль</option>
              <option>Долгосрочный банковский кредит</option>
              <option>Краткосрочный банковский кредит</option>
              <option>Кредиторская задолженность</option>
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="formStateSum">
            <Form.Label>Сумма</Form.Label>
            <Form.Control as="input" name="sum" ref={register} required />
          </Form.Group>
        </Form.Row>

        {capitalIncrease ? (
          <Form.Row>
            <Form.Group as={Col} controlId="formCapitalIncrease">
              <Form.Label>Увеличение капитала (прибыль, доход)</Form.Label>
              <Form.Control as="select" name="type" ref={register} required>
                <option>...</option>
                <option>Выручка</option>
                <option>Прочие доходы</option>
                <option>Дивиденды к получению</option>
                <option>Проценты к получению</option>
              </Form.Control>
            </Form.Group>
          </Form.Row>
        ) : null}

        {capitalDecrease ? (
          <Form.Row>
            <Form.Group as={Col} controlId="formCapitalDecrease">
              <Form.Label>Уменьшение капитала (убыток, расходы)</Form.Label>
              <Form.Control as="select" name="type" ref={register} required>
                <option>...</option>
                <option>Себестоимость продукции, работ, услуг</option>
                <option>Коммерческие расходы</option>
                <option>Управленческие расходы</option>
                <option>Проценты к уплате</option>
                <option>Прочие расходы</option>
                <option>Налог на прибыль</option>
                <option>Дивиденды к начислению</option>
              </Form.Control>
            </Form.Group>
          </Form.Row>
        ) : null}

        {cashIncrease ? (
          <Form.Row>
            <Form.Group as={Col} controlId="formCashIncrease">
              <Form.Label>Поступление денежных средств</Form.Label>
              <Form.Control as="select" name="type" ref={register} required>
                <option>...</option>
                <option>Поступления по текущей деятельности</option>
                <option>Поступления по инвестиционной деятельности</option>
                <option>Поступления по финансовой деятельности</option>
              </Form.Control>
            </Form.Group>
          </Form.Row>
        ) : null}

        {cashDecrease ? (
          <Form.Row>
            <Form.Group as={Col} controlId="formCashDecrease">
              <Form.Label>Платежи (выбытие) денежных средств</Form.Label>
              <Form.Control as="select" name="type" ref={register} required>
                <option>...</option>
                <option>Платежи по текущей деятельности</option>
                <option>Платежи по инвестиционной деятельности</option>
                <option>Платежи по финансовой деятельности</option>
              </Form.Control>
            </Form.Group>
          </Form.Row>
        ) : null}

        {costsCalculation ? (
          <Form.Row>
            <Form.Group as={Col} controlId="formCostsCalculation">
              <Form.Label>Статья калькуляции</Form.Label>
              <Form.Control as="select" name="type" ref={register} required>
                <option>...</option>
                <option>Материальные затраты (Прямые затраты)</option>
                <option>Оплата труда и соцстрахование (Прямые затраты)</option>
                <option>Амортизация (Постоянные затраты)</option>
                <option>Услуги и работы (Прямые затраты)</option>
                <option>Услуги и работы (Косвенные затраты)</option>
                <option>Реклама (Косвенные затраты)</option>
                <option>Материальные затраты (Косвенные затраты)</option>
                <option>
                  Оплата труда и соцстрахование (Косвенные затраты)
                </option>
                <option>
                  Управленческие расходы (другие Косвенные затраты)
                </option>
                <option>Коммерческие расходы (другие Косвенные затраты)</option>
              </Form.Control>
            </Form.Group>
          </Form.Row>
        ) : null}

        <Button variant="outline-secondary" type="submit">
          Провести операцию
        </Button>
      </form>
    </div>
  );
}





let modalForm = [
  { name: "title", title: "Название", type: "text" },
  { name: "comment", title: "Комментарий", type: "textarea" },
];

function NewProjectForm() {
  const { register, handleSubmit, reset, watch } = useForm(); //, watch, errors
  const applicationState = useSelector(selectApplication);
  const dispatch = useDispatch();
  const email = applicationState?.email;
  const user = applicationState?.user;
  const avatarUrl = applicationState?.avatarUrl;

  function setFormObject(data) {

    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = firebase.database().ref(userEmail).child('posts').push().key;

      let postObject = {
        id: idPost, // "accountingwithprofitscash",
        title: data.title, // "Планирование и бюджетирование по трем формам"
        theme: "Планирование и бюджетирование", //data.currentProjectTitle.length > 2 ? data.currentProjectTitle : 'Проект',
        answer: "Операции и прогнозная отчетность",
        comment: data.comment, // "Лекционный пример",
        type: "accountingwithprofitscash",
        content: [],
        quizString: "Операции и прогнозная отчетность",
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      var updates = {};
      updates[
        "/usersCraft/" + userEmail + "/posts/" + idPost
      ] = postObject;
      // updates['/openmedia/' + idPost] = postObject;
      // updates["/usersTemplates/projects/" + userEmail + '/' + idPost] = postObject;

      return firebase
        .database()
        .ref()
        .update(updates)
        .then(() => {
          window.location.reload()
          //      DoSavedSuccessfully(true);
          //      doEditPostTitleComment(false);
        });
    }

    //    setProjectComment(data.comment);
  }

  return (
    <form onSubmit={handleSubmit(setFormObject)}>
      {modalForm.map((item) => (
        <InputGroup className="mb-3" size="sm" key={item.name}>
          <InputGroup.Prepend>
            <InputGroup.Text style={{ width: "8rem" }}>
              {item.title}
            </InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            key={item.name}
            name={item.name}
            type={item.type}
            ref={register}
            as={item.type === "textarea" ? "textarea" : "input"}
          />
        </InputGroup>
      ))}
      <Button variant="outline-secondary" type="submit" size="sm">
        Сохранить
      </Button>
    </form>
  );
}

function RecordsList({ records }) {
  const { register, handleSubmit, reset, watch } = useForm();
  const [filter, setFilter] = useState({ d: null, k: null });

  const onSubmit = (data) => {
    console.log(data);
    setFilter({ d: data.d, k: data.k });
  };

  let uniqueD = [...new Set(records.map((item) => item.d))];
  let uniqueK = [...new Set(records.map((item) => item.k))];

  // console.log(uniqueD); console.log(uniqueK); console.log(filter);

  let filteredDrecords = !!filter.d
    ? records.filter((item) => item.d === filter.d)
    : records.map((item) => item);
  let filteredKrecords = !!filter.k
    ? filteredDrecords.filter((item) => item.k === filter.k)
    : filteredDrecords.map((item) => item);

  return (
    <Container>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Form.Row>
          <Form.Group as={Col} controlId="formAssetIncreaseLiabiliesDecrease">
            <Form.Label>Активы +, Пассивы -</Form.Label>
            <Form.Control as="select" name="d" ref={register} required>
              {uniqueD.map((item) => (
                <option key={item}>{item}</option>
              ))}
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="formAssetDecreaseLiabiliesIncrease">
            <Form.Label>Активы -, Пассивы +</Form.Label>
            <Form.Control as="select" name="k" ref={register} required>
              {uniqueK.map((item) => (
                <option key={item}>{item}</option>
              ))}
            </Form.Control>
          </Form.Group>
        </Form.Row>
        <Container>
          <Row>
            <Col>
              <Button variant="outline-secondary" type="submit">
                Применить фильтр
              </Button>
            </Col>
            <Col>
              <Button
                variant="outline-secondary"
                onClick={() => setFilter({ d: null, k: null })}
              >
                Все операции
              </Button>
            </Col>
          </Row>
        </Container>
      </form>

      {filteredKrecords.map((row, index) => (
        <Row key={index}>
          <Col xs={2}>
            <small>{row.period}</small>
          </Col>
          <Col xs={3}>
            <small>{row.d}</small>
          </Col>
          <Col xs={3}>
            <small>{row.k}</small>
          </Col>
          <Col xs={4}>
            <small>{row.sum}</small>
            {!!row?.type ? <small>{" " + row.type}</small> : <small> </small>}
          </Col>
        </Row>
      ))}
    </Container>
  );
}

